h2 {
  opacity: .5;
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 8vh;
  font-weight: 800;
  line-height: 1;
  display: block;
}

h3 {
  opacity: .5;
  margin: 0;
  padding: 0 20px;
  font-family: sans-serif;
  font-size: 5vh;
  font-weight: 800;
  line-height: 1;
}

.slide__intro {
  z-index: 1;
  max-width: 50%;
  -webkit-backdrop-filter: blur();
  backdrop-filter: blur();
  background: linear-gradient(to bottom right, #00145066, #000a28cc);
  border-radius: 10px;
  padding: 30px;
  animation-delay: 1.5s;
  position: absolute;
  top: 4vh;
  left: 120px;
}

.slide__intro--desc {
  text-transform: none;
  text-shadow: 2px 2px 1px #000000bf;
  padding-top: 10px;
  font-family: Commissioner, sans-serif;
  font-size: 1vw;
  line-height: 1.5;
}

.slide__intro--info {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.slide__intro--icons {
  pointer-events: auto;
  flex-direction: row;
  align-items: center;
  gap: 20px;
  display: flex;
}

.slide__intro--icon {
  opacity: .9;
  fill: #fff;
  cursor: pointer;
  background-color: #0a145080;
  border: 3px solid #ffffffbf;
  border-radius: 5px;
  padding: 10px;
  transition: transform .25s ease-in-out, opacity .25s ease-in-out;
  display: inline-block;
  position: relative;
}

.slide__intro--icon:hover, .slide__intro--icon:focus {
  opacity: 1;
  transform: scale(1.1);
}

.slide__intro--icon:hover:after {
  content: attr(data-tooltip);
  height: 22px;
  color: #fff;
  white-space: nowrap;
  text-transform: uppercase;
  background-color: #0a145080;
  border-radius: 4px;
  padding: 3px 8px;
  font-size: 10px;
  position: absolute;
  top: 110%;
  left: 50%;
  transform: translateX(-50%);
}

.slide__intro--icon-svg {
  max-width: 4vh;
  max-height: 4vh;
}

